@media (max-width: 1200px) {

    body::before {
        max-width: 200px;
    }

    .header__inner {
        justify-content: space-between;
        padding-top: 18px;
    }

    .logo {
        width: 56px;
        height: 56px;
    }

    .logo--size {
        width: 249px;
    }

    .header-button {
        @include fz20;
        font-weight: 500;
        border-radius: $border-radius-18;
        padding: 4px 26px;
        width: 160px;
        min-height: 56px;
    }

    .header-button2 {
        border-radius: $border-radius-18;
        padding: 0;

        &::before {
            border-radius: $border-radius-18;
        }

        .wrapper {
            padding: 16px 31px;
            border-radius: $border-radius-18;
        }
    }

    .main-info__inner {
        display: none;
    }

    .mobile-info {
        display: block;
    }

    .logo__link {
        margin-left: initial;
    }

    .rubberfield {
        display: none;
    }

    .social {
        right: 53px;
    }

    .social-icons {
        top: 84px;
        padding: initial;
    }

    .social-icon {
        padding: 10px;
    }

    .pattern-anim {
        display: none;
    }

    .card__items {
        position: initial;
        bottom: initial;
        right: initial;
        flex-direction: column;
        align-items: start;
        padding: 0 15px;
    }

    .card__item {
        box-shadow: $box-shadow-6;

        &:hover,
        &:focus {
            box-shadow: initial;

            .card__inner {
                opacity: initial;

            }
        }
    }

    .card__img {

        img,
        source {
            width: 262px;
            height: 178px;
        }
    }

    .card__item:nth-child(1) {
        margin-right: initial;
        margin-bottom: 20px;
    }

    .card__inner {
        opacity: 1;
        @include bgBlur2;
    }

    .card__title {
        @include fz24;
        font-weight: 500;
        margin-bottom: 18px;
    }

    .card__btn {
        @include fz20;
        font-weight: 500;
        padding: 8px 32px;
    }

    .modal-content {
        min-height: 174px;
        border-radius: $border-radius-18;
        margin: 10px 0;
    }

    .modal-content__inner {
        margin-bottom: 18px;
    }

    .modal-content__icon-sprite {
        width: 28px;
        height: 28px;
    }

    .modal-content__link {
        @include fz20;
        font-weight: 500;

        &-icon {
            width: 24px;
            height: 24px;
        }

        &-title {
            margin-left: 8px;
        }
    }

    .blog-info {
        padding: 50px 0 62px;
    }

    .title {
        @include fz48;
    }

    .subtitle {
        @include fz36;
    }

    .blog-box {
        @include fz18;
        padding-bottom: 60px;

        &__img {
            height: 200px;
            margin-bottom: 20px;
        }

        &__img--position {
            img {
                width: 100%;
                height: 100%;
            }
        }

        &__title {
            @include fz24;
            margin-bottom: 10px;
        }

        &__subtitle {
            margin-bottom: 32px;
        }

        &__title,
        &__subtitle,
        &__block,
        &__info-title {
            padding: 0 14px;
        }

        &__achievements {
            margin-bottom: 26px;
        }

        &__item-inner {
            margin-bottom: 0;
        }

        &__item-icon {
            svg {
                width: 24px;
                height: 24px;
            }
        }

        &__item-title {
            @include fz20;
            margin-left: 8px;
        }

        &__item-counter {
            @include fz20;
        }

        &__btn-more {
            @include fz18;
            font-weight: 500;
            height: 40px;
            padding: 5px 0;
            border-radius: $border-radius-12;
        }

        &__gallery {
            height: 720px;
            margin-bottom: 24px;

            &-counter {
                min-width: 162px;
                height: 36px;
                padding-top: 2px;
                padding-left: 10px;
                padding-right: 9px;
            }

            &-wrap {
                height: 180px;

                &::after {
                    background-size: 34px;
                }
            }

            &-preview {
                height: 180px;
            }
        }
    }

    .blog-box-items {
        &__link {
            height: 180px;

            &::after {
                background-size: 34px;
            }
        }
    }

    .close-text {
        font-size: 0;

        &::before {
            @include fz18;
        }
    }

    .blog-box__info-title {
        @include fz24;
        font-weight: 500;
        margin-bottom: 12px;
    }

    .blog-box__skills {
        margin-bottom: 24px;
    }

    .blog-box__skills-icon {
        margin-bottom: 6px;

        svg {
            width: 42px;
            height: 42px;
        }
    }

    .blog-box__info-title--none-mobile {
        display: none;
    }

    .blog-box__vso {
        display: none;
    }
}

@media (max-width: 1200px) and (max-height: 600px) {
    .mobile-info {
        padding-top: 8px;
        margin-bottom: 8px;
    }

    .card__item:nth-child(1) {
        margin-bottom: 6px;
    }
}

@media (max-width: 940px) {

    .social-icon {

        &:hover,
        &:focus {
            background-color: initial;
            color: $white;
            box-shadow: initial;
        }
    }

    .header__inner--margin {
        display: none;
    }

    .logo-mobile {
        display: block;
    }

    .blog-box {
        &__inner {
            border-radius: $border-radius-24;
        }


        &__achievements {
            margin-bottom: 12px;
        }

        &__item {
            padding: 0 14px;
            margin-bottom: 8px;
        }

        &__item-icon {
            svg {
                width: 20px;
                height: 20px;
            }
        }

        &__item-title {
            @include fz20;
            font-weight: 500;
            margin-left: 6px;
        }

        &__gallery {
            height: 558px;

            &-wrap {
                width: calc(33.33%);
                height: 186px;
            }

            &-preview {
                height: 186px;
            }
        }

        &__btn-more {

            &:hover {
                color: $black;
                background-color: $white-gray;
            }
        }

    }

    .blog-box-items {
        &__link {
            width: calc(33.33%);
            height: 186px;

            &::after {
                background-size: 34px;
            }
        }
    }

    .err__title {
        @include fz96;
        margin-bottom: 10px;
    }

    .err__subtitle {
        @include fz24;

        a {
            max-width: 304px;
            padding: 6px;
        }
    }
}

@media (max-width: 800px) {
    .blog-box__skills {
        margin-bottom: 12px;
    }

    .blog-box__skills-item {
        max-width: initial;
        width: auto;
        margin: 8px 4px;
    }
}

@media (max-width: 780px) {
