*,
*::before,
*::after {
	box-sizing: border-box;
}

html {
	scroll-behavior: smooth;
}

html,
body {
	position: relative;
}

body {
	max-width: 100vw;
	width: 100%;
	position: relative;
	font-family: 'Rubik', 'Poppins', 'Montserrat', sans-serif;
	@include fz24;
	color: $black;
	text-decoration: none;
	@include bgLinerColor;
	position: relative;
	overflow-x: hidden;
	z-index: $z-index-0;

	&::before {
		content: "";
		position: fixed;
		max-width: 390px;
		width: 100%;
		height: 100%;
		top: 0;
		right: 0;
		background-color: rgba($white-gray, 0.8);
		@include bgBlur;
		overflow: hidden;
		z-index: $z-index-minus-1;
		transition: all $transition-1;
	}
}

.preloader {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	background-color: $main-color-dark-400;
	color: $white;
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: $z-index-10;
	transition: all 0.7 ease-out;
}

.preloader-none {
	opacity: 0;
	pointer-events: none;
}

.preloader__inner {
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
}

.preloader__logo {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: $z-index-2;

	svg {
		width: 80px;
		height: 80px;
		animation-duration: 2s;
		animation-delay: 0s;
		animation-name: preloadLogoAnim;
		animation-iteration-count: infinite;
	}
}

.preloader__circl {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 80px;
	height: 80px;
	border-radius: 50%;
	animation-duration: 2.1s;
	animation-delay: 0s;
	animation-name: preloadLogoBgAnim;
	animation-iteration-count: infinite;
}

.preloader__dots {
	width: 50px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding-top: 30px;
}

.preloader__dot {
	width: 10px;
	height: 10px;
	border-radius: 50%;
	background-color: rgba($white, 1);
	animation-duration: 1s;
	animation-delay: 0s;
	animation-iteration-count: infinite;
	scale: 0;

	&:nth-child(1) {
		animation-name: preloadDotAnim3;
	}

	&:nth-child(2) {
		animation-name: preloadDotAnim2;
	}

	&:nth-child(3) {
		animation-name: preloadDotAnim1;
	}
}

@keyframes preloadDotAnim1 {
	0% {
		scale: 0;
		opacity: 1;
	}

	70% {
		scale: 1;
		opacity: 1;
	}

	100% {
		scale: 0;
		opacity: 0;
	}
}

@keyframes preloadDotAnim2 {
	0% {
		scale: 0;
		opacity: 1;
	}

	50% {
		scale: 1;
		opacity: 1;
	}

	100% {
		scale: 0;
		opacity: 0;
	}
}

@keyframes preloadDotAnim3 {
	0% {
		scale: 0;
		opacity: 1;
	}

	30% {
		scale: 1;
		opacity: 1;
	}

	100% {
		scale: 0;
		opacity: 0;
	}
}

@keyframes preloadLogoAnim {
	0% {
		scale: 1;
	}

	14% {
		scale: 0.9;
	}

	100% {
		scale: 1;
	}
}

@keyframes preloadLogoBgAnim {
	0% {
		scale: 1;
		background-color: rgba($white, 0);
	}

	90% {
		scale: 4;
		background-color: rgba($white, 0.6);
	}


	100% {
		background-color: rgba($white, 0);
		scale: 1;
		opacity: 0;
	}
}

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

a {
	color: $black;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

p {
	margin: 18px 0;
	text-indent: 42px;
	text-align: justify;
}

button {
	color: $white;
	@include no-btn;
	display: inline-block;
}

img {
	display: flex;
	pointer-events: none;
	background-repeat: no-repeat;
	background-position: center center;
}

svg {
	display: block;
}


select {
	font: inherit;
	letter-spacing: inherit;
	word-spacing: inherit;
	appearance: none;
	border: none;
	line-height: 31px;
}

::selection {
	background: $main-color;
	color: $white;
}

::-moz-selection {
	background: $main-color;
	color: $white;
}

::-webkit-selection {
	background: $main-color;
	color: $white;
}

.noactive {
	overflow: hidden;
}

.btnup {
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	bottom: 0;
	left: 0;
	height: 100%;
	width: 60px;
	background-color: rgba($black, 0.1);
	@include bgBlur;
	cursor: pointer;
	opacity: 0;
	pointer-events: none;
	transition: $transition-1;
	z-index: $z-index-8;

	&__inner {
		display: flex;
		align-items: center;
		justify-content: center;

		svg {
			width: 18px;
			height: 18px;
			color: $white;
		}
	}
}

.btnup:hover {
	background-color: rgba($black, 0.2);
}

.body-link {
	overflow: initial;
}

.btn {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
	@include fz24;
	font-weight: 700;
	color: $white;
	background-color: $main-color;
	text-transform: uppercase;
	border-radius: $border-radius-24;
	padding: 10px 37px;
	width: 200px;
	height: 64px;
	cursor: pointer;
	@include btnTransition;
	text-decoration: none;

	&:hover,
	&:focus {
		@include btnHover;
	}

	&:active {
		box-shadow: none;
	}
}

.container {
	max-width: 1742px;
	width: 100%;
	margin: 0 auto;
	padding: 0 15px;
}

.container-2 {
	max-width: 1752px;
	width: 100%;
	margin: 0 auto;
	padding: 0 15px;
}

.overflow,
.overflow2 {
	overflow: hidden;
}

.err {
	text-align: center;
	color: $white;
	position: absolute;
	top: 212px;
	left: 0;
	right: 0;
	padding: 0 25px;
	pointer-events: none;
}

.err__title {
	@include fz128;
	margin-bottom: 20px;
}

.err__subtitle {
	display: flex;
	flex-direction: column;
	align-items: center;

	a {
		display: block;
		max-width: 450px;
		width: 100%;
		pointer-events: all;
		margin-top: 12px;
		padding: 12px 30px;
		color: $white;
		border-radius: $border-radius-24;
		transition: color $transition-1, background-color $transition-1, box-shadow $transition-1;

		&:hover,
		&:focus {
			@include btnHover;
		}
	}
}

.header {
	max-width: 100%;
	width: 100%;
}

.header__inner {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	padding-top: 90px;
	position: relative;
	z-index: $z-index-3;
	transition: all $transition-1;
}

.header__inner--padding {
	justify-content: space-between;
}

.logo__link {
	margin-left: auto;
	z-index: $z-index-4;
}

.rubberfield {
	max-width: 580px;
	width: 100%;
}

.header__inner--margin {
	margin: initial;
}

.logo {
	display: block;
	width: 64px;
	height: 64px;
	color: $white;
	transition: color $transition-1;
}

.logo-mobile {
	display: none;
}

.logo--size {
	width: 284.23px;
}

.logo__link:hover,
.logo__link:focus {
	.logo {
		color: $black-green;
	}
}

.header-button {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
	white-space: nowrap;
	@include fz24;
	font-weight: 700;
	color: $white;
	background-color: $black;
	border-radius: $border-radius-24;
	padding: 10px 37px;
	width: 202px;
	height: 100%;
	min-height: 64px;
	@include btnTransition;

	&:hover,
	&:focus {
		@include btnHover;
	}
}

.main-info {
	pointer-events: none;
}

.max {
	@include fz300;
	color: $black-green;
	position: fixed;
	top: 313px;
	left: 458px;
	transition: all $transition-1;
}

.photo {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	min-height: 100%;
	z-index: $z-index-2;

	&__inner {
		position: relative;
	}

	img,
	source {
		position: fixed;
		top: 0;
		left: 0;
		object-fit: cover;
		height: 100%;
	}
}

@media (min-height: 1080px) {
	.photo {

		img,
		source {
			min-height: 1167px;
			height: 100%;
		}
	}
}

.dev {
	@include fz270;
	color: $white;
	position: fixed;
	top: 540px;
	left: 458px;
	text-shadow: $box-shadow-3;
	transition: all $transition-1;
}

.max,
.dev {
	z-index: $z-index-1;
}

.social {
	height: 100%;
	min-height: 100vh;
	position: absolute;
	top: 0;
	right: 146px;
	z-index: $z-index-0;
	transition: all $transition-1;
}

.social__inner {
	height: 100%;
	@include dFlex;
	flex-direction: column;
}

.social-anim-line {
	overflow: hidden;
}

.social-anim-line__dotted {
	width: 2px;
	background-color: $white;
	position: relative;

	&::before,
	&::after {
		content: "";
		@include posCenterLeft;
		width: 2px;
		height: 23px;
		@include bgLinerColor;
		border-radius: $border-radius-24;
		animation-iteration-count: infinite;
	}
}

.social-anim-line {
	height: 100%;

	.social-anim-line__dotted {
		height: 100%;

		&::before {
			animation-duration: 5s;
			animation-delay: 0s;
			animation-name: bgAnimItem;
		}

		&::after {
			animation-duration: 5s;
			animation-delay: 1.2s;
			animation-name: bgAnimItem;
		}
	}
}

@keyframes bgAnimItem {
	0% {
		top: -24px
	}

	100% {
		top: 120%
	}
}

.social-icons {
	@include dFlex;
	flex-direction: column;
	padding: 24px 0;
	@include posCenterLeft;
	top: 210px;
	@include bgBlur;
}

.social-icon {
	color: $white;
	background-color: transparent;
	padding: 15px;
	@include btnTransition;
	border-radius: $border-radius-12;

	&__behance,
	&__codepen,
	&__theme {
		width: 32px;
		height: 32px;
	}

	&:hover,
	&:focus {
		@include btnHover;
	}
}

.modal {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba($black, 0.8);
	opacity: 0;
	pointer-events: none;
	@include bgBlur;
	z-index: $z-index-9;
	transition: opacity $transition-2;
}

.modal__inner {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100%;
	z-index: $z-index-8;
}

.modal-content {
	max-width: 450px;
	width: 100%;
	min-height: 219px;
	background-color: $main-color;
	@include bgBlur;
	border-radius: $border-radius-24;
	margin: 20px 0 20px 0;
	padding: 15px 10px;
	color: $white;
	@include fz24;
	font-weight: 700;
	scale: 0;
	opacity: 0;
	transition-delay: 0.1s;
	transition: opacity $transition-3, scale $transition-3;

	&__link:hover,
	&__link:focus {
		@include btnHover;
	}
}

.modal-content__inner {
	@include dFlex;
	justify-content: space-between;
	margin-bottom: 41px;
	padding-left: 8px;
	padding-right: 3px;
}

.modal-content__icon-sprite {
	display: block;
	width: 35px;
	height: 35px;
}

.modal-content__link {
	@include dFlex;
	color: $white;
	border-radius: $border-radius-24;
	padding: 10px 12px;
	@include btnTransition;

	&-icon {
		width: 35px;
		height: 35px;
	}

	&-title {
		margin-left: 12px;
	}
}

.active {
	pointer-events: auto;
	opacity: 1;

	.modal-content {
		scale: 1;
		opacity: 1;
	}
}

.card__items {
	@include dFlex;
	position: fixed;
	bottom: 64px;
	right: 195px;
	z-index: $z-index-3;
	transition: all $transition-1;
}

.card__item {
	@include dFlex;
	justify-content: center;
	transform: translateZ(0);
	border-radius: $border-radius-24;
	border: 2px solid $main-color;
	box-shadow: $box-shadow-5;
	overflow: hidden;
	position: relative;
	transition: all $transition-1;

	&:hover,
	&:focus {
		box-shadow: $box-shadow-6;

		.card__inner {
			opacity: 1;
		}
	}
}

.card__img {

	img,
	source {
		width: 428px;
		height: 260px;
		object-fit: cover;
	}
}

.card__item:nth-child(1) {
	margin-right: 50px;
}

.card__inner {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	@include dFlex;
	justify-content: center;
	flex-direction: column;
	@include bgBlur;
	border-radius: $border-radius-20;
	background-color: rgba($black, 0.7);
	opacity: 0;
	transition: opacity $transition-2;
}

.card__title {
	@include fz34;
	font-weight: 700;
	margin-bottom: 28px;
	color: $white;
}

.card__btn {
	@include fz24;
	font-weight: 700;
	color: $white;
	border-radius: $border-radius-24;
	background-color: $main-color;
	padding: 4px 46px;
}

.pattern-anim {
	@include posCenterLeft;
	top: 0;
	pointer-events: none;
	z-index: $z-index-minus-1;
}

.pattern-anim__inner {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 1920px;
	height: 1080px;
	position: relative;
}

.pattern-anim__icon {
