@keyframes animPattern {
	0% {
		scale: 1;
	}

	50% {
		scale: 0.95;
	}

	100% {
		scale: 1;
	}
}

.blog-info {
	padding: 90px 0 130px 0;
	pointer-events: none;
	position: relative;
	z-index: $z-index-1;
}

.title {
	@include fz128;
	color: $black-green;
}

.subtitle {
	@include fz96;
	color: $white;
	text-shadow: $box-shadow-3;
}

.title,
.subtitle {
	word-wrap: break-word;
}

.blog-box {
	height: 100%;
	position: relative;
	transform: translateZ(0);
	padding-bottom: 200px;
	z-index: $z-index-8;

	&__inner {
		max-width: 100%;
		width: 100%;
		min-height: 100px;
		height: 100%;
		background-color: $white;
		transform: translateZ(0);
		border-radius: $border-radius-34;
		position: relative;
		box-shadow: $box-shadow-5;
		overflow: hidden;
	}

	&__img {
		height: 494px;
		margin-bottom: 54px;
		overflow: hidden;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			scale: 1;
			transition: scale $transition-1;
		}

		&:hover {
			img {
				scale: 1.02;
			}
		}
	}

	&__img--position {
		img {
			width: initial;
			height: initial;
		}
	}

	&__title {
		@include fz45;
		margin-bottom: 24px;
	}

	&__subtitle {
		margin-bottom: 48px;
	}

	&__title,
	&__subtitle,
	&__block,
	&__info-title {
		padding: 0 52px;
	}

	&__achievements {
		@include dFlex;
		flex-wrap: wrap;
		justify-content: space-around;
		@include fz32;
		margin-bottom: 45px;
	}

	&__link {
		display: flex;
		justify-content: center;
		max-width: 200px;
		width: 100%;
		margin-bottom: 52px;
		margin-left: auto;
		margin-right: auto;
		transition: color $transition-1;

		&:hover,
		&:focus {
			color: $main-color;
		}
	}

	&__item {
		scale: 1;
		transition: color $transition-1, scale $transition-1, background-color $transition-1;
	}

	&__item-inner {
		@include dFlex;
		margin-bottom: 6px;
	}

	&__item-icon {
		svg {
			width: 46px;
			height: 46px;
		}
	}

	&__item-title {
		font-weight: 600;
		margin-left: 22px;
	}

	&__item-counter {
		font-weight: 500;
		text-align: center;
	}

	&__item-title,
	&__item-counter {
		display: block;
		pointer-events: none;
	}

	&__item:hover,
	&__item:focus {
		scale: 1.02;

		.blog-box__item-icon,
		.blog-box__item-title,
		.blog-box__item-counter {
			color: $main-color;
		}
	}

	&__block {
		height: 0;
		// padding: 0 32px;
		margin: 0 10px;
		overflow: hidden;
	}

	.btn-more {
		padding: 0 10px 10px;
	}

	&__btn-more {
		@include fz24;
		text-align: center;
		font-weight: 700;
		max-width: 100%;
		width: 100%;
		height: 54px;
		color: $black;
		background-color: $white-gray;
		padding: 8px 0;
		border-radius: $border-radius-16;
		transition: color $transition-1, background-color $transition-1;

		&:hover {
			color: $white;
			background-color: $main-color;
		}
	}

	&__gallery {
		max-width: 100%;
		width: 100%;
		height: 786px;
		display: flex;
		flex-wrap: wrap;
		transform: translateZ(0);
		margin-bottom: 40px;
		position: relative;
		overflow: hidden;

		&-counter {
			text-align: right;
			position: absolute;
			bottom: 0;
			right: 0;
			color: $white;
			min-width: 170px;
			height: 40px;
			transform: translateZ(0);
			border-top-left-radius: $border-radius-12;
			background-color: rgba($black, 0.5);
			@include bgBlur;
			padding-top: 4px;
			padding-left: 10px;
			padding-right: 12px;
			pointer-events: none;
			@include fz18;
			z-index: $z-index-8;
		}

		&-wrap {
			width: calc(25%);
			height: 262px;
			position: relative;
			overflow: hidden;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				z-index: $z-index-1;
				background-color: rgba($black, 0.6);
				pointer-events: none;
				opacity: 0;
			}

			&::after {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				z-index: $z-index-6;
				color: $white;
				pointer-events: none;
				background-image: url('../img/sprite.svg#icon-fullscreen');
				background-repeat: no-repeat;
				background-position: center center;
				background-size: 55px;
				opacity: 0;
			}

			&::before,
			&::after {
				transition: opacity $transition-1;
			}
		}

		&-wrap:nth-child(n+1):nth-child(-n+12) {
			display: flex;
		}

		&-preview {
			object-fit: contain;
			height: 262px;
		}

		&-wrap:hover {

			&::before,
			&::after {
				opacity: 1;
			}
		}

	}

}

