.header-button2 {
	--duration: 7s;
	--easing: linear;
	--c-color-1: #{rgba(#941aff, .7)};
	--c-color-2: #5CD16F;
	--c-color-3: #c11797;
	--c-color-4: #{rgba(#FFE81A, .7)};
	--c-shadow-inset-top: #{rgba(#5CD16F, .9)};
	--c-shadow-inset-bottom: #{rgba(#89f09a, .8)};
	--c-radial-inner: #34A947;
	--c-radial-outer: #379c48;
	--c-color: #fff;
	-webkit-tap-highlight-color: transparent;
	-webkit-appearance: none;

	position: relative;
	cursor: pointer;
	border: none;
	border-radius: $border-radius-24;
	padding: 0;
	margin: 0;
	text-align: center;
	@include fz24;
	font-weight: 700;
	box-shadow: 0 0 22px 10px rgba($main-color-dark-100, 0.38);
	color: var(--c-color);
	background: radial-gradient(circle, var(--c-radial-inner), var(--c-radial-outer) 80%);
	transition: all $transition-1;

	&::before {
		content: '';
		pointer-events: none;
		position: absolute;
		z-index: 3;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		border-radius: $border-radius-24;
		box-shadow: inset 0 3px 12px var(--c-shadow-inset-top), inset 0 -3px 4px var(--c-shadow-inset-bottom);
	}

	.wrapper {
		-webkit-mask-image: -webkit-radial-gradient(white, black);
		overflow: hidden;
		border-radius: 24px;
		padding: 14px 40px;

		span {
			display: inline-block;
			position: relative;
			z-index: 1;
			transition: color $transition-1;
		}

		.circle {
			position: absolute;
			left: 0;
			top: 0;
			width: 40px;
			height: 40px;
			border-radius: 50%;
			filter: blur(var(--blur, 8px));
			background: var(--background, transparent);
			transform: translate(var(--x, 0), var(--y, 0)) translateZ(0);
			animation: var(--animation, none) var(--duration) var(--easing) infinite;

			&.circle-1,
			&.circle-9,
			&.circle-10 {
				--background: var(--c-color-4);
			}

			&.circle-3,
			&.circle-4 {
				--background: var(--c-color-2);
				--blur: 14px;
			}

			&.circle-5,
			&.circle-6 {
				--background: var(--c-color-3);
				--blur: 16px;
			}

			&.circle-2,
			&.circle-7,
			&.circle-8,
			&.circle-11,
			&.circle-12 {
				--background: var(--c-color-1);
				--blur: 12px;
			}

			&.circle-1 {
				--x: 0;
				--y: -40px;
				--animation: circle-1;
			}

			&.circle-2 {
				--x: 92px;
				--y: 8px;
				--animation: circle-2;
			}

			&.circle-3 {
				--x: -12px;
				--y: -12px;
				--animation: circle-3;
			}

			&.circle-4 {
				--x: 80px;
				--y: -12px;
				--animation: circle-4;
			}

			&.circle-5 {
				--x: 12px;
				--y: -4px;
				--animation: circle-5;
			}

			&.circle-6 {
				--x: 56px;
				--y: 16px;
				--animation: circle-6;
			}

			&.circle-7 {
				--x: 8px;
				--y: 28px;
				--animation: circle-7;
			}

			&.circle-8 {
				--x: 28px;
				--y: -4px;
				--animation: circle-8;
			}

			&.circle-9 {
				--x: 20px;
				--y: -12px;
				--animation: circle-9;
			}

			&.circle-10 {
				--x: 64px;
				--y: 16px;
				--animation: circle-10;
			}

			&.circle-11 {
				--x: 4px;
				--y: 4px;
				--animation: circle-11;
			}

			&.circle-12 {
				--blur: 14px;
				--x: 52px;
				--y: 4px;
				--animation: circle-12;
			}
		}
	}
}

.header-button2:hover {
	color: $white;
	box-shadow: initial;
}

@keyframes circle-1 {
	33% {
		transform: translate(0px, 16px) translateZ(0);
	}

	66% {
		transform: translate(12px, 64px) translateZ(0);
	}
}

